.LazyLoad {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden; }
  .LazyLoad img {
    width: 100%;
    height: auto; }

.loadingImage {
  -webkit-animation: opacityBlink infinite 1.5s linear;
          animation: opacityBlink infinite 1.5s linear; }

@-webkit-keyframes opacityBlink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.3; }
  100% {
    opacity: 1; } }

@keyframes opacityBlink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0.3; }
  100% {
    opacity: 1; } }



